body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}


.content
{
  background: #eeeeee;
  max-width: 100%;
}
.container-news
{
  width: 100%;
  
}


header
{
    max-width: 100%;
}
.btn {
    border-radius: 0 !important;
  }
.Logo
{
    width: 100%;
    height: 100px;
    background: white;
    justify-content: center;
    display:flex;
}
.frame-img
{
    width: 80%;
    height: auto;
    text-align: center;
    padding-top: 10px
}
.group-account-button
{
    width: 20%;
    text-align: center;
    display: flex;
  align-items: center;
  justify-content: center;
}
.btnLogin{
    background-color: #4CAF50;
    border: none;
    color: white;
     width: 30%;
     
}
.btnRegister{
    background-color: #1199c3;
    border: none;
    color: white;
     width: 30%;
     
}


.bg-black
{
    height: 50px;
    width: 100%;
    display: flex;
    background: black;
    justify-content: center;
    font-size: 20px;
    line-height: 30px;
    padding: 0 0 px 0;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 500;
    color: white;
    text-transform: uppercase;
   
}
.list-link, .dropdown
{
    display: block;
    padding-top: 12px;
    padding-right: 30px;
}

.list-link:hover
{
    cursor: pointer;
    color: #ff8000;
}
.dropdown:hover{
    cursor: pointer;
    color: #ff8000;

}

@media only screen and (max-width: 850px)
{

  .container{
    padding: 0;
  }

    .card .card-img-top {
        width:60px;
    }
    .card-group {
        flex-direction: row;
        flex-wrap:  wrap;
    }
    .container-hair-style
    {
        position: relative;
    }

    .group-image
    {
        width: 100%;
    }
    .button-left
    {
        position: absolute;
        top: 25%;
        right: 90%;
    }
    .button-right
    {
        position: absolute;
        top: 25%;
        left: 90%;
    }
    .media-group
    {
        width: 100%;
        display: grid;
     grid-template-columns: 50% 50%;
      grid-template-rows: 278px 278px;
    }
    .group-image-store
    {
        width: 50%;
    }
    .commitment
    {
        flex-direction: column;
    }
    .circle
    {
        width: 80px !important;
        height: 80px;
    }

    

}
.card
{
    border: none;
}
.card-img-top
{
    width: 90px;
    text-align: center;
}
.card-body
{
    padding: 3px 5px 0px 0px;
  
}
.card-title{
    color: #7e7e7e;
    line-height: 20px;
    padding: 5px 0 5px 0;
}
.card-text
{
    font-size: 25px;
    color: #1b1b1b;
    line-height: 30px;
    padding: 0 0 px 0;
    font-family: 'Roboto Condensed', sans-serif;
    padding: 5px 0 5px 10px;
    font-weight: 900;
}
h1
{
    margin-bottom: 0;
    font-size: 25px;
    line-height: 30px;
    font-family: 'Oswald', sans-serif;
    text-transform: uppercase;
    color: #2e2d2d;
    padding: 15px 10px 20px 10px;
}


section{
    margin-top: 4px;
    padding-top: 5px;
    background: white;

}

.container-hair-style
{
    display: flex;
    width: 100%;
    padding: 0 15px 0 15px;
    flex-wrap: wrap;

}
.group-image
{
    width: 40%;
    padding-left: 20px;

}
.button-next
{
    width: 10%;
}

.imagestyle img{
    width: 100%;
    height: auto;

}
.fa-angle-right,
.fa-angle-left
{
    text-align: center;
    font-size: 60px;
    padding-top: 30px;
}

.Title{
    display: flex;
    justify-content: space-between;

}
.form-group
{
    padding-top: 15px;
    padding-right: 10px;
}

.media-group
{
    width: 100%;
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    grid-template-rows: 278px;
    
}
.media{
    flex-direction: column;
}
.media img{
    width: 100%;
    height: auto;
    padding: 10px;
}
.media-body
{
    padding-left: 40px;
}

.yt-sub
{
   display: flex;
   justify-content: center;
    
   
    width: 100%;
    padding: 10px;
    text-transform: uppercase;
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 24px;
}
.yt-sub img{
    width: 50%;
    height: auto;
}
.yt-sub p{
    padding-top: 15px;
    padding-right: 10px;
}
.play-video{
    position: relative;
}
.play-video span{
    width: 100%;
    height: 100%;
    position: absolute;
    background: url(https://uploads-ssl.webflow.com/5b1e3a3e988ef9847080c704/5b1e3a3e988ef978d480c7c4_np-play-730809-000000%403x.png) no-repeat;
    background-position: 50% 50%;
    background-size: auto;
    -webkit-transition: -webkit-transform .7s ease-in-out;
    transition: -webkit-transform .7s ease-in-out;
    transition: transform .7s ease-in-out;
    transition: transform .7s ease-in-out, -webkit-transform .7s ease-in-out;
    opacity: 0.9;
    background-size: 18%;
    z-index: 1;
}
.group-image-review
{
    width: 26,67%;
    height: auto;
    padding-left: 20px;
    text-align: center;
}
